<template>
  <div>
    <div class="container-fluid">
      <div class="row">
        <div class="col-6 col-xl-4">
          <div class="tools">
            <div class="d-flex p-2">
              <span></span>
              <el-checkbox-group v-model="checkedEdu" @change="filterChange">
                <el-checkbox v-for="edu in eduBox" :label="edu" :key="edu">{{edu}}</el-checkbox>
              </el-checkbox-group>
            </div>
            <div class="d-flex p-2">
              <span></span>
              <el-input-number v-model="salary" :min="-1" size="mini" @change="filterChange"></el-input-number>
            </div>
            <div class="d-flex p-2">
              <span></span>
              <el-input-number v-model="exp" :min="-1" :max="10" size="mini" @change="filterChange"></el-input-number>
            </div>
            <div class="d-flex p-2">
              <span></span>
              <el-input v-model="resp" placeholder="# " size="mini" style="max-width:300px" @keyup.enter.native="filterChange"></el-input>
            </div>
            <div class="d-flex p-2">
              <span></span>
              <el-input v-model="welfare" placeholder="# " size="mini" style="max-width:300px" @keyup.enter.native="filterChange"></el-input>
            </div>
            <div class="d-flex p-2">
              <span></span>
              <el-date-picker v-model="createDate" type="daterange" align="right" unlink-panels range-separator="" start-placeholder="" end-placeholder="" :picker-options="pickerOptions" size="mini" @change="filterChange">
              </el-date-picker>
            </div>
            <div class="d-flex p-2">
              <span></span>
              <el-date-picker v-model="updateDate" type="daterange" align="right" unlink-panels range-separator="" start-placeholder="" end-placeholder="" :picker-options="pickerOptions" size="mini" @change="filterChange">
              </el-date-picker>
            </div>
            <div class="p-2 small text-info">
              <div>
                2018910
              </div>
              <div>
                
              </div>
              <div>Web </div>
            </div>
          </div>
        </div>
        <div class="col-6 col-xl-8">
          <div class="map-data">
            <el-amap vid="amapDemo" :zoom="zoom" :center="center" class="amap-demo">
              <!-- <el-amap-search-box class="search-box"></el-amap-search-box> -->
              <el-amap-info-window :position="currentWindow.position" :content="currentWindow.content" :visible="currentWindow.visible" :events="currentWindow.events">
              </el-amap-info-window>
              <el-amap-marker :key="index" v-for="(marker, index) in markers" :position="marker.position" :events="marker.events" :visible="marker.visible" :draggable="marker.draggable" :vid="index"></el-amap-marker>
            </el-amap>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import mapData from "./map.data.json";
export default {
  data() {
    return {
      eduBox: ["", "", "", ""],
      checkedEdu: ["", "", "", ""],
      resp: "",
      salary: -1,
      exp: -1,
      welfare: "",
      originMarkers: [],
      createDate: "",
      updateDate: "",
      pickerOptions: {
        shortcuts: [
          {
            text: "",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit("pick", [start, end]);
            }
          },
          {
            text: "",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit("pick", [start, end]);
            }
          },
          {
            text: "",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit("pick", [start, end]);
            }
          }
        ]
      },
      currentWindow: {
        position: [120.125068, 30.225363],
        content: "",
        events: {
          close: function() {
            this.currentWindow.visible = false;
          }.bind(this)
        },
        visible: true
      },
      zoom: 12,
      center: [120.125068, 30.225363],
      markers: []
    };
  },
  created() {
    this.originMarkers = mapData;
    this.markers = mapData.map(this.mapResult);
  },
  methods: {
    mapResult(item) {
      var { geo: { lat, lon } } = item;
      return {
        position: [lon, lat],
        events: {
          click: function(item) {
            var {
              geo: { lat, lon },
              company: { name },
              salary,
              jobName,
              updateDate,
              createDate,
              eduLevel,
              welfare,
              workingExp,
              timeState,
              positionURL,
              feedbackRation,
              resumeCount,
              address
            } = item;
            this.currentWindow = Object.assign(this.currentWindow, {
              position: [lon, lat],
              content: `
              <div class="small">
                    <div>${name}</div>
                    <div>${jobName}</div>
                    <div class="text-primary">${eduLevel.name} ${salary} ${
                workingExp.name
              }</div>
                    <div>${welfare.join(",")}</div>
                    <div>${updateDate}</div>
                    <div>${createDate}</div>
                    <div>${timeState}</div>
                    <div>${(feedbackRation * 100).toFixed(2)} %</div>
                    <div>resumeCount${resumeCount}</div>
                    <div>${address}</div>
                    <div><a href="${positionURL}" target="_blank"></a></div>
              </div>
                  `,
              visible: true
            });
          }.bind(this, item)
        }
      };
    },
    filterChange() {
      // true => keep
      this.markers = this.originMarkers
        .filter(eachItem => {
          // console.log(eachItem);
          var {
            // geo: { lat, lon },
            company,
            salary,
            jobName,
            updateDate,
            createDate,
            eduLevel,
            welfare,
            workingExp,
            timeState,
            positionURL,
            feedbackRation,
            resumeCount,
            address,
            responsibility
          } = eachItem;
          // 
          // if(eduLevel.name.indexOf('') == -1 && eduLevel.name.indexOf('') == -1){
          //   console.log(eduLevel.name);
          // }
          if (this.checkedEdu.indexOf(eduLevel.name) == -1) {
            return false;
          }
          //
          var reg = /[\u4e00-\u9fa5]/g;
          //  0 1 
          if (workingExp.name != "") {
            var respAry = workingExp.name.replace(reg, "").split("-");
            if (workingExp.name === "1" || workingExp.name === "") {
              respAry = [0, 0];
            }
            if (respAry.length === 2) {
              var begin = parseInt(respAry[0]);
              var end = parseInt(respAry[1]);
              if (this.exp !== -1 && (this.exp < begin || this.exp > end)) {
                return false;
              }
            } else {
              console.log(
                ` ${company.name} ${workingExp.name}`
              );
              return false;
            }
          }
          // 
          if (this.salary !== -1 || salary === "") {
            var salaryReg = /^[0-9]{1,}k-[0-9]{1,}k$/;
            if (salaryReg.test(salary.toLowerCase())) {
              var [minSalary, maxSalary] = salary.split("-").map(item => {
                return parseInt(item.replace("k", ""));
              });
              if (this.salary > maxSalary) {
                return false;
              }
            } else {
              console.log(` ${salary} `);
              return false;
            }
          }
          //
          var respAry = this.resp.split("#").map(item => {
            return item.trim();
          });
          for (const i of respAry) {
            if (responsibility.toLowerCase().indexOf(i.toLowerCase()) == -1) {
              return false;
            }
          }
          //
          var conWelfares = this.welfare.split("#").map(item => {
            return item.trim();
          });
          for (const i of conWelfares) {
            if (
              welfare
                .join("#")
                .toLowerCase()
                .indexOf(i.toLowerCase()) == -1
            ) {
              return false;
            }
          }
          //
          if (this.createDate && this.createDate.length > 0) {
            var [startDate, endDate] = this.createDate;
            // console.log(this);
            var tdate = new Date(createDate);
            if (tdate < startDate || tdate > endDate) {
              return false;
            }
          }
          //
          if (this.updateDate && this.updateDate.length > 0) {
            var [startDate, endDate] = this.updateDate;
            var tdate = new Date(updateDate);
            if (tdate < startDate || tdate > endDate) {
              return false;
            }
          }
          return true;
        })
        .map(this.mapResult);
    }
  }
};
</script>
<style lang="less" scoped>
.tools {
  height: 100vh;
  width: 100%;
  & > div > span {
    min-width: 150px;
  }
}
.map-data {
  width: 100%;
  height: 100vh;
}
.search-box {
  position: absolute;
  top: 25px;
  left: 20px;
}
</style>





